let Notification = window.Notification;
/**
 * @desc 
 */
function getAuthorityOfNotification() {
    if (Notification !== undefined && Notification.permission !== 'granted') {
        Notification.requestPermission().then(result => {
            if (result === 'granted') {
                noticeToUser({
                    title: '!',
                    body: 'NC Cloud ',
                });
            }
        });
    }

}

/**
 *
 * @param params {object} {
 *   title: 'string'
 *   dir: 'string'  auto, ltr, or rtl
 *   body: 'string' 
 *   icon: 'string'  URL
 *   onClose 'function' 
 * }
 */
function noticeToUser(params) {
    if (Notification === undefined) return;

    if (params.title === undefined) {
        console.error('');
        return;
    }

    let notification = new Notification(params.title, {
        dir: params.dir || 'auto',
        body: params.body,
        icon: params.icon,
    });

    notification.onclose = function () {
        params.onClose();
    };

}

export {
    getAuthorityOfNotification,
    noticeToUser,
};
