import { getSafeRandom } from './utils'

export default function ajax(options) {
    var url = options.url || '', //
        type = (options.type || 'get').toLowerCase(), //,get
        data = options.data, //|| JSON.stringify({}), //
        contentType = options.contentType || '', //
        dataType = options.dataType || '', //
        async = options.async === undefined ? true : options.async, //true.
        timeOut = options.timeOut, //
        before = options.before || function () { }, //
        error = options.error || function () { }, //
        success = options.success || function () { }; //
    var timeout_bool = false, //
        timeout_flag = null, //
        xhr = null; //xhr

    // JSONP
    function createJsonp() {
        var script = document.createElement('script'),
            timeName = new Date().getTime() + Math.round(getSafeRandom() * 1000),
            callback = 'JSONP_' + timeName;

        window[callback] = function (data) {
            clearTimeout(timeout_flag);
            document.body.removeChild(script);
            success(data);
        };
        script.src = url + (url.indexOf('?') > -1 ? '&' : '?') + 'callback=' + callback;
        script.type = 'text/javascript';
        document.body.appendChild(script);
        setTime(callback, script);
    }

    //
    function setTime(callback, script) {
        if (timeOut !== undefined) {
            timeout_flag = setTimeout(function () {
                if (dataType === 'jsonp') {
                    delete window[callback];
                    document.body.removeChild(script);
                } else {
                    timeout_bool = true;
                    xhr && xhr.abort();
                }
                console.log('timeout');
            }, timeOut);
        }
    }

    // XHR
    function createXHR() {
        //IE6XMLHttpRequestMSXMLActiveX
        //XHR
        function getXHR() {
            if (window.XMLHttpRequest) {
                return new XMLHttpRequest();
            } else {
                //IEActiveX
                var versions = ['Microsoft', 'msxm3', 'msxml2', 'msxml1'];
                for (var i = 0; i < versions.length; i++) {
                    try {
                        var version = versions[i] + '.XMLHTTP';
                        return new ActiveXObject(version);
                    } catch (e) { }
                }
            }
        }

        //
        xhr = getXHR();
        xhr.open(type, url, async);
        //
        if (type === 'post' && !contentType) {
            //postcontent-Type application/x-www-form-urlencoded
            xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded;charset=UTF-8');
        } else if (contentType) {
            xhr.setRequestHeader('Content-Type', contentType);
        }
        // 
        if (options.headers && options.headers['Identifications']) {
            xhr.setRequestHeader('Identifications', options.headers['Identifications']);
        }

        //  
        if (options.headers && options.headers['transcode']) {
            xhr.setRequestHeader('transcode', options.headers['transcode']);
        }

        // CURX  xx
        if (options.headers && options.headers['crux']) {
            xhr.setRequestHeader('crux', options.headers['crux']);
        }

        xhr.withCredentials = true;
        //
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4) {
                if (timeOut !== undefined) {
                    //abort()onreadystatechange
                    //timeout_bool
                    if (timeout_bool) {
                        return;
                    }
                    clearTimeout(timeout_flag);
                }
                if ((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
                    success(xhr.responseText, xhr);
                } else {
                    error(xhr.status, xhr.statusText);
                }
            }
        };
        //
        xhr.send(type === 'get' ? null : data);
        setTime(); //
    }
    //setData();
    before();
    if (dataType === 'jsonp') {
        createJsonp();
    } else {
        createXHR();
    }
}